import React, {useState, useRef} from "react";
import Input from '../components/Input/Input'
import './InputsPage.css'

function InputsPage(){
  const [currentActive, setCurrentActive] = useState('')
  const inputRef = React.createRef()

  const changeActive = ()=>{
    let cur = currentActive
    setCurrentActive(cur==='active'? '':'active')

    if(inputRef.current && cur===''){
      inputRef.current.focus()
    }
  }
  
  return (
    <div className="input-pages">
      <Input className={'search-container '+ currentActive} inputRef={inputRef} placeholder="请输入您要搜索的内容">
        <button type="button" className="search-btn" onClick={changeActive}>
          {/* <i className='search-icon'></i> */}
          <svg className="search-icon" t="1636954864643" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2672" width="200" height="200"><path d="M416 192C537.6 192 640 294.4 640 416S537.6 640 416 640 192 537.6 192 416 294.4 192 416 192M416 128C256 128 128 256 128 416S256 704 416 704 704 576 704 416 576 128 416 128L416 128z" p-id="2673"></path><path d="M832 864c-6.4 0-19.2 0-25.6-6.4l-192-192c-12.8-12.8-12.8-32 0-44.8s32-12.8 44.8 0l192 192c12.8 12.8 12.8 32 0 44.8C851.2 864 838.4 864 832 864z" p-id="2674"></path></svg>
        </button>
      </Input>
    </div>
  )
}

export default InputsPage