import Steps from "../components/Steps/Steps"
import {useState, useEffect} from 'react'

function StepsPage(){
  const stepItems = [
    1,2,3,4
  ]
  const [currentActive, setCurrentActive] = useState(0)

  const prevClick = ()=>{
    let count = currentActive
    if(count>0)
    setCurrentActive(--count)
  }
  const prevBtnDisabled = currentActive===0
  const nextBtnDisabled = currentActive===stepItems.length-1
  const nextClick = ()=>{
    let count = currentActive
    if(count<stepItems.length-1)
    setCurrentActive(++count)
  }

  useEffect(() => {
    console.log(currentActive)
  })
  const onClickItem = (value)=>{
    alert('请查看控制台以查看选中的值')
    console.log('%c'+ value, "background:#2396ef;color:#fff;padding:4px 10px;")
  }
  return (
    <div>
      <Steps 
        stepItems={stepItems} 
        currentActive={currentActive} 
        onClickItem={onClickItem}
        width='600px'/>

      <div className='btn-container'>
        <button disabled={prevBtnDisabled} onClick={()=>prevClick()}>上一步</button>   
        <button disabled={nextBtnDisabled} onClick={()=>nextClick()}>下一步</button> 
      </div>
    </div>
  )
}

export default StepsPage