import React from 'react'
import './Steps.css'

class Steps extends React.Component {
  constructor(props){
    super(props)
    this.state = {}
  }
  render(){
    const {stepItems, currentActive, width, onClickItem} = this.props
    const computedWidth = ()=>{
      return (currentActive)/(stepItems.length-1) *100 + '%'
    }
    return (
      <div className='step-container' style={{width: width}}>
        <div className="progress" style={{width: computedWidth()}}></div>
        {
          stepItems.map((item, index)=>{
            return(
              <div 
                className={`step-item ${currentActive >= index && 'active'}`} 
                key={item}
                onClick={()=>onClickItem(item)}>
                {item}
              </div>
            )
          })
        }
      </div>
    )
  }
}

export default Steps